}
.cards{
    display: flex;
    justify-content: space-evenly;
    margin: auto;
    margin-top: 70px;
    max-width: 1350px;
}
.card{
    position: relative;
    width: 490px;
    height: 540px;
    background-color: white;
    border-radius: 20px;
    text-align: left;
    box-shadow: 0px 2px 50px rgba(0, 77, 167, 0.2);
}
/* Card Image Start */
.card-icon{
    position:absolute;
    bottom: 42px;
    right: 0px;
}
.card-icon .french-img{
    height: 392px;
}
.card-icon .vedic-img{
    height: 362px;
}
/* Card Image End*/

/* Card Text Section Start */
.card-txt{
    margin-left: 30px;
    margin-top: 40px;
}
.card-heading{
    font-size: 35px;
    font-weight: 500;
    margin-top: 20px;
}
.card-sub-heading{
    color:#7B7B7B;
    font-size: 17px;
    font-weight: 500;
}
.card-info{
    font-size: 20px;
    font-weight: 500;
    margin-top: 30px;
    margin-bottom: 20px;
}
.card .rightArrow{
    position: relative;
    font-size: 22px;
    top: 3px;
}
.point{
    display: flex;
    font-size: 20px;
    margin-top: 18px;
    margin-bottom: 18px;
    font-weight: 400;
}
.point img{
    margin-right: 10px;
    width: 15px;
}
.bullet{
    margin-right: 10px !important;
    width: 15px !important;
}
.points{
    margin-top: 20px;
    margin-left: 7px;
}
/* Card Text Section End */

.card-btn{
    position: absolute;
    width: 100%;
    height: 70px;
    bottom: 0px;
    left: 0px;
    background-color: #2D3092;
    border-radius: 0px 0px 20px 20px;
    color: #FFFFFF;
    font-size: 23px;
    transition: 0.3s;
}
.card-btn:hover{
    background-color: #F7FAFB;
    color: #2d3092;
    border: 2px solid #2d3092;
}
.card-btn:hover .rightArrow{
    -webkit-animation: goRight 3s forwards;
            animation: goRight 3s forwards;
}
@-webkit-keyframes goRight {
    from{
        left: 0px;
    }
    to{
        left: 40px;
    }
}
@keyframes goRight {
    from{
        left: 0px;
    }
    to{
        left: 40px;
    }
}
.card-info-img{
    width: 380px;  
    margin-top: 20px; 
}
