  }
  .input{
    box-sizing: border-box;
    height: 45px;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    border: 1px solid #BCBCBC;
    border-radius: 10px;
    transition: 0.25s;
    font-size: 15px;
    padding-left: 10px;
  }
  .dropdown-list{
    overflow-y: scroll;
    max-height: 300px;
  }
  .dropdown-list-elements{
    position: relative;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    border: 1px solid #BCBCBC;
    font-size: 15px;
    padding-left: 10px;
    cursor: pointer;
  }
  .dropdown-list-elements:hover{
      background-color: gray;
  }
  .input:focus{
    outline: none;
    border: 1px solid #000F61;
  }
  .input-label{
    position: absolute;
    color: #282828;
    font-size: 14px;
    font-weight: 500;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    left: 32px;
    top: 15px;
    transition: 0.25s;
  }
  .input-box.active .input-label,
  .input:focus + .input-label,
  .input:active + .input-label{
    top: -6px;
    left: 28px;
    background-color: white;
    font-size: 12px;
  }
  
  /* When the value is present inside the input box */
  .input:not([value=""]) + .input-label{
    top: -6px;
    left: 28px;
    background-color: white;
    font-size: 12px;
  }
