.input-box-frenchschool{
    position: relative;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .input-frenchschool{
    box-sizing: border-box;
    height: 45px;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    border: 1px solid #BCBCBC;
    border-radius: 10px;
    transition: 0.25s;
    font-size: 15px;
    padding-left: 10px;
  }
  .input-frenchschool:focus{
    outline: none;
    border: 1px solid #000F61;
  }
  .input-label-frenchschool{
    position: absolute;
    color: #282828;
    font-size: 14px;
    font-weight: 500;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    left: 32px;
    top: 15px;
    transition: 0.25s;
  }
  .input-box.active .input-label-frenchschool,
  .input-frenchschool:focus + .input-label-frenchschool,
  .input-frenchschool:active + .input-label-frenchschool{
    top: -6px;
    left: 28px;
    background-color: white;
    font-size: 12px;
  }
  
.input-frenchschool:not([value=""]) + .input-label-frenchschool{
    top: -6px;
    left: 28px;
    background-color: white;
    font-size: 12px;
  }
